@use './variables'

.switchLanguage 
    padding: 0px
    width: 140px
    height: 40px
    background-color: #D5E4F7
    box-shadow: 0px 0px 6px rgba(206, 189, 255, 0.01), 0px 0px 5px rgba(206, 189, 255, 0.06), 0px 0px 5px rgba(206, 189, 255, 0.2), 0px 0px 3px rgba(206, 189, 255, 0.34), 0px 0px 2px rgba(206, 189, 255, 0.39), 0px 0px 0px rgba(206, 189, 255, 0.4)
    border-radius: 40px
    top: 28px
    font-family: 'Gotham Pro'
    font-weight: 400
    display: flex
    justify-content: space-between
    align-items: center


.switchLanguage > button 
    margin-right: 5px
    border: none
    cursor: pointer
    font-size: 14px
    line-height: 17px
    color: #515151
    font-family: Gotham Pro
    font-weight: 500
    width: 50%
    height: 40px
    text-align: center
    display: flex
    justify-content: center
    align-items: center


.switchLanguage > button.active 
    padding: 10px 16px 10px 36px
    gap: 8px
    width: 80px
    height: 40px
    border-radius: 80px
    background-color: #ffffff
    color: #515151
    font-weight: 600
    position: relative
    margin-right: 0px


.switchLanguage > button.active:before 
    content: ""
    background-image: url("../../assets/switch.svg")
    background-size: 14px 14px
    background-repeat: no-repeat
    display: block
    width: 14px
    height: 14px
    position: absolute
    top: 50%
    left: 16px
    transform: translateY(-50%)


.switchLanguage > button:last-child 
    margin-left: 5px


.switchLanguage > button.active.rus 
    padding: 10px 16px 10px 36px
    gap: 8px
    width: calc(100% - 100px)
    height: 40px
    border-radius: 80px
    background-color: #ffffff
    color: #515151
    font-weight: 600
    position: relative
    margin-right: 0px


.switchLanguage > button.active.eng 
    background-color: #51606F

.switchLanguage > button.transparent 
    background-color: transparent 
    
.languages
    font-weight: 500
    text-align: center
    vertical-align: top
    font-size: 14px
    font-family: Gotham Pro
    line-height: 20%
    color: #51606f