@use './variables' as *

.profile 
    display: flex
    flex-direction: column
    gap: 56px


    h2.form-header
        font-size: 28px
        font-family: Gotham Pro
        font-weight: 400
        color: #233240
        
    .form 
        display: flex
        flex-direction: column
        padding: 40px
        gap: 32px
        background: #FFFFFF
        box-shadow: 0px 0px 6px rgba(206, 189, 255, 0.01), 0px 0px 5px rgba(206, 189, 255, 0.06), 0px 0px 5px rgba(206, 189, 255, 0.2), 0px 0px 3px rgba(206, 189, 255, 0.34), 0px 0px 2px rgba(206, 189, 255, 0.39), 0px 0px 0px rgba(206, 189, 255, 0.4)
        border-radius: 20px
        width: 359px


    .profileInput 
        display: flex
        flex-direction: column
        gap: 8px


    label.form-title
        font-family: Gotham Pro
        font-weight: 150
        color: #51606F
        font-size: 13px


.input 
    border: none
    padding: 16px
    background: #E8F2FF
    border-radius: 12px
